<template>
    <div>
        <a-form @submit="handleSubmit" :autoFormCreate="(form)=>{this.form = form}" layout='inline'>
            <a-form-item
                    label='名称'
                    :labelCol="{ span: 5 }"
                    :wrapperCol="{ span: 19 }"
                    fieldDecoratorId="name"
                    :fieldDecoratorOptions="{rules: [{ required: true, message: 'Please input your note!' }]}"
            >
                <a-input/>
            </a-form-item>
            <a-form-item>
                <a-button type='primary' htmlType='submit'>
                    提交
                </a-button>
            </a-form-item>
        </a-form>
        <div style="margin-bottom: 10px;margin-top: 10px;margin-left: 8px" v-if="hasSelected">
            {{`已选 ${selectedRowKeys.length} 项`}}
        </div>
        <a-table :pagination="false"
                 rowKey="id"
                 size="small"
                 :rowSelection="{selectedRowKeys: selectedRowKeys, onChange: onSelectChange}"
                 :columns="columns"
                 :dataSource="data"/>
    </div>
</template>
<script>
    import {getAuthMapList} from '@/api/authMap'
    import {getAuthRole, createAuthRole, updateAuthRole} from '@/api/authRole'

    export default {
        props: {
            id: {
                required: false,
                type: Number,
                default() {
                    return 0;
                }
            }
        },
        data() {
            return {
                form: {},
                data: [],
                columns: [
                    {
                        title: '权限',
                        dataIndex: 'auth_format',
                        key: 'auth_format',
                    },
                    {
                        title: '描述1',
                        dataIndex: 'description_1',
                        key: 'description_1',
                    },
                    {
                        title: '描述2',
                        dataIndex: 'description_2',
                        key: 'description_2',
                    },
                ],
                selectedRowKeys: [], // Check here to configure the default column
                loading: false,
            }
        },
        computed: {
            hasSelected() {
                return this.selectedRowKeys.length > 0
            }
        },
        methods: {
            fetch() {
                getAuthMapList({current_page: 0, filter: {}})
                    .then(res => {
                        this.data = res.data.rows;
                    })

                if (this.id) {
                    getAuthRole(this.id)
                        .then(res => {
                            this.selectedRowKeys = res.data.auth_map_ids;
                            this.form.setFieldsValue({name: res.data.name});
                        })
                }
            },
            handleSubmit(e) {
                e.preventDefault()
                let data = this.form.getFieldsValue();
                data.map_ids = this.selectedRowKeys;


                let request;
                if(this.id){
                    request=updateAuthRole(this.id,data);
                }else{
                    request=createAuthRole(data);
                }
                request.then(res=>{
                    this.$message.success('操作成功');
                })
            },
            onSelectChange(selectedRowKeys) {
                console.log('selectedRowKeys changed: ', selectedRowKeys);
                this.selectedRowKeys = selectedRowKeys
            }
        },
        mounted() {
            this.fetch();
        }
    }
</script>
