<template>
    <div>
        <table-component
                ref="table"
                @handleOperate="handleOperate"
                :columns="columns"
                :request="tableRequest">
            <template slot="top">
                <a-button type="primary"
                          @click="handleCreate()">新增
                </a-button>
                <div style="height: 10px"></div>
                <filter-component :fields="filterFields" @handleSearch="handleSearch"/>
                <div style="height: 10px"></div>
            </template>
        </table-component>
        <a-modal
                width="90%"
                :title="modalTitle"
                v-model="modalVisible"
                :footer="null"
                :destroyOnClose="true">
            <form-component :id="formId"/>
        </a-modal>
    </div>
</template>

<script>
    import {getAuthRoleList, deleteAuthRole} from '@/api/authRole'
    import tableComponent from '@/components/table'
    import filterComponent from '@/components/filter/Index'
    import FormComponent from './components/Form'

    export default {
        components: {
            filterComponent, tableComponent, FormComponent
        },
        data() {
            return {
                modalTitle: null,
                modalVisible: false,
                formId: 0,
                columns: [
                    {
                        title: '#',
                        dataIndex: 'index',
                        scopedSlots: {customRender: 'index'},
                    },
                    {
                        title: '名称',
                        dataIndex: 'name',
                        key: 'name',
                    },
                    {
                        title: '权限数量',
                        dataIndex: 'auth_count',
                        key: 'auth_count',
                    },
                    {
                        title: '最后修改日期',
                        dataIndex: 'updated_at',
                        key: 'updated_at',
                    },
                    {
                        title: '操作',
                        dataIndex: 'operation',
                        scopedSlots: {customRender: 'operation'},
                        buttons: {
                            edit: {visible: true, disable: false,},
                            delete: {
                                visible: true, disable: function (row) {
                                    return !!row.users.length;
                                },
                            },
                        }
                    }
                ],
                tableRequest: getAuthRoleList,
                filterFields: [
                    {label: '关键字', field: 'keyword'},
                ],
            }
        },
        methods: {
            handleSearch(filter) {
                this.$refs.table.handleSearch(filter)
            },
            handleCreate() {
                this.modalTitle = '新增角色';
                this.modalVisible = true;
                this.formId = null;
            },
            handleEdit(id) {
                this.modalTitle = '编辑角色';
                this.modalVisible = true;
                this.formId = id;
            },
            handleOperate(type, row) {
                switch (type) {
                    case 'create':
                        this.handleCreate();
                        break;
                    case 'edit':
                        this.handleEdit(row.id);
                        break;
                    case 'delete':
                        deleteAuthRole(row.id)
                            .then(res => {
                                this.$message.success('删除成功');
                                this.$refs.table.reFetch();
                            });
                        break;
                }
            },
        },
    }
</script>

